<template>
  <div>
    <LoHeader class="lo_header" @query="queryData" :FilterItems="FilterItems"  title="上分列表"></LoHeader>
    <div class="lo_content_box">
      <mu-load-more class="lo_content" :refreshing="refreshing" :loading="loading" style="width: 100%;"
                    @refresh="refresh" @load="load">
        <template v-for="item in tableData">
            <div class="container">
              <div class="desc">
                <div class="title">
                  <div>
<!--                    <span class="label">上分：</span>-->
                    <span class="title_1">{{item.UserName}}</span>
                  </div>
                  <div>
                    <span class="label">申请：</span>
                    <span class="time_v">{{item.CreatedAt}}</span>
                  </div>
                </div>

                <div class="priceDiv">
                  <div>
                    <span class="label">下分：</span>
                    <span class="price">{{item.Gold}}</span>
                  </div>
                  <div>
                    <span class="label">审核：</span>
                    <span class="time_v">{{item.UpdatedAt}}</span>
                  </div>
<!--                  <div class="btn">-->
<!--                    <div class="btn_01" @click="doDelDrawMoney(item)">拒绝</div>-->
<!--                    <div class="btn_01" @click="doAgreeDrawMoney(item)">同意</div>-->
<!--                  </div>-->
                </div>
              </div>
          </div>
        </template>
      </mu-load-more>
    </div>
  </div>
</template>

<script>
import LoHeader from "../../components/LoHeader";
import {SaveMoneyList} from '../../api/Finance';

export default {
  name: "SaveMoneyList",
  components: {
    LoHeader
  },
  data () {
    return {
      num: 10,
      refreshing: false,
      loading: false,

      FilterItems:[
        {label:'用户Id:',Value:0,type:"number",placeholder:"用户Id",icon:"",Name:"userId"},
        {label:'用户名:',Value:'',type:"text",placeholder:"用户名",icon:"",Name:"userName"},
        {label:'开始时间:',Value:'',type:"date",placeholder:"开始时间",icon:"",Name:"beginDay"},
        {label:'结束时间:',Value:'',type:"date",placeholder:"结束时间",icon:"",Name:"endDay"},
      ],

      PageTotal:0,
      tableData: [{UserName:'测试',Gold:1,CreatedAt:'2006-01-02 15:04:05'}],
      GroupData:{},
      query: {
        beginDay: '',
        endDay: '',
        userId:0,
        userName:'',
        pageIndex: 1,
        pageSize: 100,
        FirstId:0,
      },

      openFullscreen:false,
      formInfo:{
        OldData:{},
        OneOddsInfo:{Id:0,OddsDes:'',Odds:1,OneUserMaxBet:99999,OneUserMinBet:0,AllUserMaxBet:999999},
        // Rules:{
        //   OddsRules:[
        //     { validate: (val) => val<=0, message: '赔率必须大于0' }
        //   ],
        // }
      },

    }
  },
  methods: {
    refresh () {
      this.refreshing = true;
      this.query.pageIndex = 1;
      this.query.FirstId = 0;
      this.getData((v)=>{
        if (v==true){
          this.query.pageIndex = this.query.pageIndex+1
        }
        this.refreshing = false;
      });
    },
    load() {
      this.loading = true;
      this.getData((v)=>{
        if (v==true){
          this.query.pageIndex = this.query.pageIndex+1
        }
        this.loading = false;
      });
    },
    getData(fun){
      SaveMoneyList(this.query).then(response => {
        const { obj, code, msg } = response;
        if (code == 200) {
          if (this.query.FirstId==0) {
            this.tableData = obj.ListData;
          } else {
            this.tableData = this.tableData.concat(obj.ListData);
          }
          this.GroupData = obj.GroupData;
          this.PageTotal = obj.PageTotal;
          this.query.FirstId = obj.GroupData.FirstId;

          fun(true);
          resolve();
        } else {
          this.$toast.error(msg);
          fun(false);
        }
      }).catch(error => {
        fun(false);
      });
    },

    queryData(filterItems){
      this.query.pageIndex = 1;
      this.query.FirstId = 0;
      this.query.userName = '';
      this.query.userId =0;
      this.query.beginDay = '';
      this.query.endDay = '';

      for (let key in filterItems) {
        if (filterItems[key].Name === 'userName') {
          this.query.userName = filterItems[key].Value;
        } else if (filterItems[key].Name === 'userId') {
          this.query.userId = filterItems[key].Value;
        } else if (filterItems[key].Name === 'beginDay') {
          this.query.beginDay = filterItems[key].Value;
        }else if (filterItems[key].Name === 'endDay') {
          this.query.endDay = filterItems[key].Value;
        }
      }

      this.getData((v)=>{
        if (v==true){
          this.query.pageIndex = this.query.pageIndex+1
        }
      });
    },
   },
  created() {
    this.query.pageIndex = 1;
    this.query.FirstId = 0;
    this.getData((v)=>{
      if (v==true){
        this.query.pageIndex = this.query.pageIndex+1
      }
    });
  },
}
</script>

<style scoped>
@import url('../../assets/css/lottery_num.css');
@import url('../../assets/css/main.css');

.container {
  width: 100%;
  display: flex;
  flex-direction: row;
  border-bottom: 1px solid #F2F2F2;
  justify-content: space-between;
  padding: 5px 11px;
  margin: 0 0 10px;
  background: #fff;
}
.desc {
  color: #8B8B8B;
  padding: 2px;
  text-align: left;
  font-size: 12px;
  width: 100%;
}
.title {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
}
.title_1 {
  font-weight: bold;
  color: #333;
  overflow: hidden;
  font-size: 14px;
}
.price {
  color: red;
  font-size: 12px;
  font-weight: 900;
}
.fsid {
  color: #8B8B8B;
}

.priceDiv {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
}
.xiaol {
  color: #8B8B8B;
  color: red;
}
.kc {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.btn_01 {
  border: 1px solid #8B8B8B;
  border-radius: 3px;
  text-align: center;
  margin-left: 10px;
  padding: 3px 8px;
}
.btn {
  display: flex;
  justify-content: flex-end;
}
.main {
  margin-top: 8px;
  width: 100%;
  /*margin-top: 1.4rem;*/
  margin-bottom: 1.4rem;
}
.label {

}
.time_v{
  color: red;
  font-size: 12px;
}
</style>



